	module.exports = {"data":{"wordpressPost":{"title":"Top 10 most read in February: Eating speed and obesity, micronutrients in low-carbohydrate diets, high-carbohydrate intake and obesity, urinary bisphenol A in teenagers","content":"<p>&nbsp;</p>\n<p>Eight new entries made it into BMJ Open’s top 10 most read articles in February. New in at number one is a secondary analysis of longitudinal data by <em><a href=\"http://bmjopen.bmj.com/content/8/1/e019589\">Hurst &amp; Fukuda</a>, </em>which examined the association between lifestyle factors, including eating speed, and obesity in Japanese patients with diabetes. The authors demonstrated that slower eating speed was associated with reduced obesity, lower BMI and smaller waist circumference. Read more about this study in our <a href=\"https://blogs.bmj.com/bmjopen/2018/02/12/slow-eating-speed-may-be-linked-to-weight-loss/\">recent blog</a>.</p>\n<p><a href=\"https://blogs.bmj.com/bmjopen/files/2018/03/diet-695723_1920cropped.jpg\"><img class=\"Public domain wp-image-1242 alignright\" src=\"https://blogs.bmj.com/bmjopen/files/2018/03/diet-695723_1920cropped-300x207.jpg\" alt=\"\" width=\"321\" height=\"214\" /></a>Two new entries in the second and third positions examine aspects of dietary carbohydrate. At number two is a study by <a href=\"http://bmjopen.bmj.com/content/8/2/e018846\"><em>Zinn et al.</em></a> in which they designed two low-carbohydrate, high-fat (LCHF) meal plans for two hypothetical cases representing average Australian male and female adults. Both of the meal plans successfully exceeded the Australian/New Zealand nutrient reference value  thresholds for all nutrients apart from two instances; the researchers conclude that a well-planned LCHF diet can provide sufficient intakes of all of the micronutrients, apart from iron for females.</p>\n<p>At number three is a systematic review and meta-analysis by <em><a href=\"http://bmjopen.bmj.com/content/8/2/e018449\">Sartorius et al.</a>,</em> exploring whether high-carbohydrate intake leads to increased risk of obesity. Extracting and analysing data from observational studies, the researchers suggest that a higher proportion of carbohydrates in unrestricted diets do not increase obesity levels. However, they caution that this study has not been able to assess refined versus unrefined carbohydrate dietary intake.</p>\n<p><a href=\"http://bmjopen.bmj.com/content/8/2/e018742\"><em>Galloway et al. </em></a>are new in at number ten; They assessed the effect of a real-world dietary intervention on urinary bisphenol A (BPA) levels in teenagers. BPA, a chemical used to make plastics often used in food and beverage packaging, was detected in urine of 86% of participants at baseline.  The self-administered dietary intervention, designed to reduce BPA consumption by avoidance of foods packaged in known sources of BPA, had no effect on urine BPA levels.</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<table>\n<tbody>\n<tr>\n<td width=\"45\"><strong>Rank</strong></td>\n<td width=\"170\"><strong>Author(s)</strong></td>\n<td width=\"401\"><strong>Title</strong></td>\n</tr>\n<tr>\n<td width=\"45\">1</td>\n<td width=\"170\"><em>Hurst &amp; Fukuda </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/1/e019589\">Effects of changes in eating speed on obesity in patients with diabetes: a secondary analysis of longitudinal health check-up data</a></td>\n</tr>\n<tr>\n<td width=\"45\">2</td>\n<td width=\"170\"><em>Zinn et al.</em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/2/e018846\">Assessing the nutrient intake of a low-carbohydrate, high-fat (LCHF) diet: a hypothetical case study design</a></td>\n</tr>\n<tr>\n<td width=\"45\">3</td>\n<td width=\"170\"><em>Sartorius et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/2/e018449\">Does high-carbohydrate intake lead to increased risk of obesity? A systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"45\">4</td>\n<td width=\"170\"><em>BinDhim et al.</em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/1/e017105\">Smartphone Smoking Cessation Application (SSC App) trial: a multicountry double-blind automated randomised controlled trial of a smoking cessation decision-aid ‘app’ </a></td>\n</tr>\n<tr>\n<td width=\"45\">5</td>\n<td width=\"170\"><em>Wang et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/2/e020991\">Antibiotics for uncomplicated skin abscesses: systematic review and network meta-analysis </a></td>\n</tr>\n<tr>\n<td width=\"45\">6</td>\n<td width=\"170\"><em>Bower et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/2/e019605\">Fetal alcohol spectrum disorder and youth justice: a prevalence study among young people sentenced to detention in Western Australia</a></td>\n</tr>\n<tr>\n<td width=\"45\">7</td>\n<td width=\"170\"><em>Rochwerg et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/6/e016847\">Corticosteroids in sepsis: an updated systematic review and meta-analysis (protocol)</a></td>\n</tr>\n<tr>\n<td width=\"45\">8</td>\n<td width=\"170\"><em> Xie et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/7/6/e015735\">Risk of death among users of Proton Pump Inhibitors: a longitudinal observational cohort study of United States veterans</a></td>\n</tr>\n<tr>\n<td width=\"45\">9</td>\n<td width=\"170\"><em>O’Sullivan et al. </em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/2/e018557\">Overtesting and undertesting in primary care: a systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"45\">10</td>\n<td width=\"170\"><em>Galloway et al.</em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/2/e018742\">An engaged research study to assess the effect of a &#8216;real-world dietary intervention on urinary bisphenol A (BPA) levels in teenagers</a></td>\n</tr>\n</tbody>\n</table>\n<p><em><br />\nMost read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.6--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"a0c4c064-f364-504a-be71-33ebadc5aad6"}}

